.SmallAbout {
    height: 90vh;
    background-image: url('/Images/Table.jpg');
    background-size: 100% 100%;
}

.BackgroundImage {
    position: absolute;
    width: 100%;
    height: 90vh;
}


.Overlay {
    position: relative;
    background-color: rgba(0,0,0,0.7);
    height: 100%;
    width: 100%;
    text-align: center;
    align-items: center;
}

.AboutUs {
    position: absolute;
    left: 40%;
    font-size: 25px;
    color: white;
    word-wrap: break-word;
    break-after: auto;
    margin-top: 50px;
    background-color: rgba(0,0,0,0);
    width: 20%;
    min-width: 200px;

}
.Title {
    position: absolute;
    font-size: 35px;
    color: white;
    word-wrap: break-word;
    break-after: auto;
    margin-top: 150px;
    background-color: rgba(0,0,0,0);
    left: 37%;
    width: 26%;
    min-width: 240px;
}
.Paragraph {
    position: absolute;
    font-size: 18px;
    color: white;
    word-wrap: break-word;
    break-after: auto;
    margin-top: 250px;
    background-color: rgba(0,0,0,0);
    left: 40%;
    width: 20%;
    min-width: 200px;
}

.Button {
    position: absolute;
    border: 2px solid purple;
    background-color: rgba(0,0,0,0.5);
    color: purple;
    font-size: 25px;
    font-weight: bold;
    margin: 450px 0px 0px 0px;
    left: 40.5%;
    width: 19%;
    min-width: 190px;
    height: 65px;
}

.Button:hover {
    background-color: purple;
    color: white;
    cursor: pointer;
} 



@media ( max-width: 880px) {
    .SmallAbout {
        height: 90vh;
        background-image: url('/Images/Table.jpg');
        background-size: 100% 100%;
        text-align: center;
    }
    
    .BackgroundImage {
        position: absolute;
        width: 100%;
        height: 90vh;
        left: 0%;
    }
    
    
    
    
    .Overlay {
        position: relative;
        background-color: rgba(0,0,0,0.7);
        height: 100%;
        width: 100%;
        text-align: center;
        align-items: center;
    }
    
    .AboutUs {
        position: relative;
        text-align: center;
        left: 0%;
        font-size: 25px;
        color: white;
        word-wrap: break-word;
        break-after: auto;
        margin: 0px 0px 0px 0px;
        background-color: rgba(0,0,0,0);
        width: initial;
        min-width: 200px;
        padding-top: 10%;
    
    }
    .Title {
        position: relative;
        font-size: 35px;
        color: white;
        word-wrap: break-word;
        break-after: auto;
        margin: 10% 0px 0px 0px;
        background-color: rgba(0,0,0,0);
        left: 0%;
        width: initial;
        min-width: 240px;
    }
    .Paragraph {
        position: relative;
        font-size: 14px;
        color: white;
        word-wrap: break-word;
        break-after: auto;
        margin: 10% 0px 0px 10%;
        background-color: rgba(0,0,0,0);
        left: 0%;
        width: 80%;
        min-width: 100px;
    }
    
    .Button {
        position: relative;
        border: 2px solid purple;
        background-color: rgba(0,0,0,0.5);
        color: purple;
        font-size: 25px;
        font-weight: bold;
        margin: 10% 0px 0px 0px;
        left: 0%;
        width: 19%;
        min-width: 190px;
        height: 65px;
    }
    
    .Button:hover {
        background-color: purple;
        color: white;
        cursor: pointer;
    } 

} 